<template>
  
  <v-stepper v-model="currentStep">
    <camera-dialog @captureClose="onCaptureClose" @cancelClose="onCancelClose"></camera-dialog>

    <v-snackbar v-model="snackbar" color="info">
      Member #{{currentStep}}, Similarity: {{member.similarity}}
      <v-btn dark flat @click.native="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>

    <v-stepper-header>
      <template v-for="n in steps">
        <v-stepper-step
          :complete="currentStep > n"
          :key="`${n}-step`"
          :step="n"
        >
          Member No.{{ n }}
        </v-stepper-step>

        <v-divider
          v-if="n !== steps"
          :key="n"
        ></v-divider>
      </template>
    </v-stepper-header>

    <v-stepper-items 
      v-touch="{
        left: () => swipe('Left'),
        right: () => swipe('Right'),
      }"
    >
      <v-stepper-content
        v-for="n in steps"
        :key="`${n}-content`"
        :step="n"
      >
        <v-layout row fill-height>
          <v-flex xs6>
            <span class="headline"><v-btn color="primary" class="headline" @click.native="showCameraDialog(true)">{{ $t("checkIn.titleId") }}</v-btn></span>
            <span v-if="member.idVerified">{{ $t( "checkIn.idVerified") }}</span>
          </v-flex>
          <v-flex xs6>
            <span class="headline"><v-btn color="primary" class="headline" @click.native="showCameraDialog(false)">{{ $t("checkIn.titleFace") }}</v-btn></span>
            <span v-if="member.idVerified">{{ $t( "checkIn.idMatches") }}</span>
          </v-flex>
        </v-layout>  
        <v-layout row fill-height>
          <v-flex xs12>
            <v-container fluid>
              <v-layout row justify-space-between>
                <v-flex xs6>
                  <img :src="idImgSrc" class="img-responsive" :width="imgWidth" @click="showCameraDialog(true)" style="cursor: pointer;"/>
                </v-flex>
                <v-flex xs6>
                  <img :src="faceImgSrc" class="img-responsive" :width="imgWidth" @click="showCameraDialog(false)" style="cursor: pointer;"/>
                </v-flex>
              </v-layout>
            </v-container>
          </v-flex>
        </v-layout>

        <v-layout row fill-height>
          <v-flex xs12>
            <v-container fluid>
              <v-layout row justify-space-between>
                <v-flex xs3>
                  <v-text-field label="*First name" required hint="*" v-model="member.firstName" v-if="member" ></v-text-field>
                </v-flex>
<!--                 <v-flex xs3>
                  <v-text-field label="Middle name"></v-text-field> 
                </v-flex> -->
                <v-flex xs3>
                  <v-text-field label="*Last name" required hint="*" v-model="member.lastName" v-if="member" ></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
            <small>*indicates required field</small>
          </v-flex>
        </v-layout>
        <v-layout row fill-height>
          <v-toolbar
            slot="footer"
            class="mt-2"
            color="primary"
            dark
            dense
            flat
          >
            <v-flex xs12>
              <v-spacer></v-spacer>
              <v-btn class="headline" icon @click.native="prevStep(n)">
                <v-icon x-large>navigate_before</v-icon>
              </v-btn>
              <v-btn class="headline" flat @click.native="prevStep(n)">{{ $t("checkIn.previous") }}</v-btn>
              <span class="headline">{{ currentStep }} / {{ steps }}</span>
              <v-btn class="headline" flat @click.native="nextStep(n)">{{ $t("checkIn.next") }}</v-btn>
              <v-btn class="headline" icon @click.native="nextStep(n)">
                <v-icon x-large>navigate_next</v-icon>
              </v-btn>
            </v-flex>
          </v-toolbar>
        </v-layout>
      </v-stepper-content>
    </v-stepper-items>

  </v-stepper>
</template>



<script>
import CameraDialog from '@/components/CameraDialog.vue'
import { EventBus } from '@/eventBus'
import { Storage } from 'aws-amplify'
import aws_exports from '@/aws-exports'
import { mapState, mapGetters, mapActions } from 'vuex'
import { CallDialog } from '@/utils'
import store from '@/store'

export default {
  name: 'CheckIn',
  data () {
    return {
      snackbar: false
    }
  },
  created () {
    this.setCurrentStep(1)
  },
  async beforeRouteEnter (to, from, next) {
    const error = await store.dispatch('checkIn/loadMembers').catch(error => {
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      next()
    }
  },
  async beforeRouteUpdate (to, from, next) {
    const error = await store.dispatch('checkIn/loadMembers').catch(error => {      
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      next()
    }
  },
  computed: {
    imgWidth: {
      get() {
        if (this.$window.width > this.$window.height) {
          return "80%"
        } else {
          return "100%"
        }
      }
    }, 
    currentStep: {
      get() {
        return this.$store.state.checkIn.currentStep
      },
      set (val) {
        this.setCurrentStep(val)
      }
    },
    ...mapState({
      identityId: state => state.session.identityId,
      members: state => state.checkIn.members,
      faceImgSrc: state => state.checkIn.faceImgSrc,
      idImgSrc: state => state.checkIn.idImgSrc
    }),
    ...mapGetters('checkIn', {
      steps: 'steps',
      member: 'member'
    })
  },
  methods: {
    swipe (direction) {
      this.swipeDirection = direction
      if (direction === 'Left') {
        this.goNext()
      } else if (direction === 'Right') {
        this.goPrevious()
      }
    },
    prevStep (n) {
      if (n === 1) {
        this.setCurrentStep(this.steps)

      } else {
        this.setCurrentStep(n - 1)
      }
    },    
    nextStep (n) {
      if (n === this.steps) {
        this.setCurrentStep(1)
      } else {
        this.setCurrentStep(n + 1)
      }
    },
    goPrevious () {
      this.prevStep(this.currentStep)
    },
    goNext () {
      this.nextStep(this.currentStep)
    },
    showCameraDialog(isId) {
      EventBus.$emit('CameraDialog', { showDialog: true, isId });
      this.isId = isId;
    },
    onCancelClose() {
      console.log('cancelClose')
    },
    async onCaptureClose(val) {
      if (val.base64Img) {
        if (val.isId) {
          await this.putImage({img: val.base64Img, isId: true})
          await this.setIdImgSrc()
        } else {
          await this.putImage({img: val.base64Img, isId: false})
          await this.setFaceImgSrc()
        }

        if (this.member.idVerified && this.member.imgBucket && this.member.idImgKey && this.member.faceImgKey) {
          const rekogRes = await this.rekogId()

          this.member.idMatches = rekogRes.idMatches
          this.member.similarity = rekogRes.similarity
          this.member.messageCode = rekogRes.messageCode

          console.log(`similarity: ${this.member.similarity}\nmessageCode: ${this.member.messageCode}`)

          this.snackbar = true
        }

        const error = await this.saveMember(val.isId).catch(e => {
          console.error('saveMember', e)
          return e
        })

        if (error) {
          CallDialog(error)
          return
        } else {
          // this.goNext()
        }
      }
    },
    ...mapActions({
      loadMembers: 'checkIn/loadMembers',
      setCurrentStep: 'checkIn/setCurrentStep',
      setIdImgSrc: 'checkIn/setIdImgSrc',
      setFaceImgSrc: 'checkIn/setFaceImgSrc',
      putImage: 'checkIn/putImage',
      rekogId: 'checkIn/rekogId',
      saveMember: 'checkIn/saveMember',
    })
  },
  components: {
    cameraDialog: CameraDialog,
  }
}
</script>